.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 3em;
    font-family: sans-serif;
    font-style: normal;
}
h1:hover {
  color: aquamarine;
}

button {
    border: 1px solid lightgray;
    background-color: transparent;
    padding: 10px;
    border-radius: 4px;
    color: white;
}

button:hover {
    cursor: pointer;
    color: black;
    background-color: blanchedalmond;
}

button:focus {
    outline:0;
}
